.roundDate {
}
.roundDate__list {
  list-style-type: none;
}
.roundDate__wrapper {
  display: flex;
  background-color: red;
  width: 100%;
  height: 130px;
}
.roundDate__link {
  margin-left: 10px;
}
.roundDate__text {
  margin: 20px;
  font-size: 22px;
  font-weight: 600;
  color: white;
}
.roundDate__title-img {
  width: 900px;
  height: 130px;
}
.roundDate__img {
  width: 200px;
  height: 130px;
}
.roundDate__wrapper-info {
  display: flex;
  flex-direction: column;
}
.roundDate__info {
  display: flex;
  flex-direction: column;
}
.roundDate__element {
  margin: 10px 0 10px 10px;
  width: 800px;
}
.roundDate__block {
  display: flex;
}
.roundDate__lick {
  width: 500px;
  margin-left: 10px;
  &:hover {
    text-decoration: underline;
  }
}
.roundDate__title {
}
.roundDate__content {
}

.roundDate__text-correction-color {
  font-weight: 600;
  color: black;
}

.roundDate__button {
  width: 80px;
  height: 30px;
  margin-top: 20px;
  border-radius: 10%;
  background-color: aliceblue;
  &:hover {
    transition: 0.5s;
    background-color: rgba(0, 0, 0, 0.557);
    color: white;
  }
}

//////////////////////////////
///   congratulation
/// /////////////////////////

.roundDate__popup {
  display: flex;
  flex-direction: column;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid black;
  position: fixed;
  background-color: aliceblue;
}

.roundDate__popup-hidden {
  visibility: hidden;
}